
* {
    margin: 0;
    padding: 0;
}

body{
    font-family: 'Titillium Web', sans-serif;
    color: #fff !important;
}

h1, h2, h3, h4, h5 {
    margin: 0;
}

h3 {
    font-weight: 900;
    font-size: 24px;
    line-height: 29px;
}

h1 {
    font-weight: 900;
    font-size: 54px;
    line-height: 54px;
}

p {
    font-weight: normal;
    font-size: 18px;
    line-height: 22px;
    margin: 0;
}

a, a:focus, a:hover {
    text-decoration: none;
}

ul {
    list-style: none;
    margin: 0;
    padding: 0;
}

.headerArea {position: absolute;z-index: 11;left: 0;width: 100%;top: 0;padding: 30px 0;}

.banarArea {
    background-color: #000105;
    position: relative;
    z-index: 1;
    padding: 175px 0 155px;
}

.banarBgImage {
    position: absolute;
    z-index: -1;
    right: 0;
    bottom: 0;
    height: 100%;
    width: auto;
}

.banarContainer {
    max-width: 466px;
    color: #fff;
}

.navBrand img {
    display: block;
    width: 97px;
}

.navSocial {
    display: flex;
    justify-content: flex-end;
    padding: 8px 0;
}

.navSocial li a {
    display: block;
}

ul.navSocial li {
    margin-left: 30px;
}

.banarContainer h1 {
    margin-bottom: 10px;
}

.mintsBtnsBox {
    margin-top: 56px;
}

.mintsBtnsBox h2 {
    font-weight: 250;
    font-size: 54px;
    line-height: 54px;
}

.mintsBtns {
    margin-top: 10px;
    display: flex;
    flex-wrap: wrap;
}

.mintCalculate input::-webkit-outer-spin-button,
.mintCalculate input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
.mintCalculate input[type=number] {
  -moz-appearance: textfield;
}

.mintCalculate input {
    max-width: 15px;
    border: 0;
    background: transparent;
    outline: none;
    font-size: 24px;
    line-height: 29px;
    text-align: center;
    letter-spacing: 0.2em;
    text-transform: uppercase;
    color: #fff;
    margin: 0 4px;
}

.mintCalculate form {
    display: contents;
}

.mintCalculate {
    display: flex;
    align-items: center;
    background: linear-gradient(265.44deg, rgba(203, 9, 231, 0.85) 0%, rgba(50, 195, 198, 0.85) 100%);
    border-radius: 5px;
    min-width: 220px;
    height: 50px;
    justify-content: center;
}

.mintCalculate span {
    font-size: 24px;
    text-align: center;
    letter-spacing: 0.2em;
    text-transform: uppercase;
    color: #FFFFFF;
    line-height: 1;
    display: flex;
    cursor: pointer;
}

.plusSign {
    position: relative;
    top: -1px;
}

.mintBtn {
    font-weight: bold;
    font-size: 18px;
    line-height: 22px;
    letter-spacing: 0.2em;
    text-transform: uppercase;
    position: relative;
    z-index: 1;
    width: 177px;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 5px;
    background-image: linear-gradient(45deg, #32C3C6, #CB09E7);
    color: #fff;
    box-shadow: 0 0 10px #ffffff52;
}

.mintBtn:after {
    z-index: -1;
    left: 4px;
    width: auto;
    right: 4px;
    top: 4px;
    bottom: 4px;
    border-radius: 5px;
    background: #000;
    content: "";
    position: absolute;
    box-shadow: inset 0 0 10px #ffffff52;
    transition: .3s ease all;
}

.mintsBtns .mintBtn {
    margin-right: 20px;
}

.mintBtn:hover:after {
    opacity: 0;
}

.mintBtn:hover {
    color: #fff;
}

.roadmapContainer {
    padding: 80px 0 70px;
    background: #040F1D;
}

.roadmapBox h3 {
    margin-bottom: 30px;
}

.singleRoadmap {
    position: relative;
    padding: 42px 0 0 97px;
    margin-bottom: 40px;
}

.roadMonth {
    position: absolute;
    z-index: 2;
    left: 0;
    top: 0;
    width: 150px;
    height: 90px;
    box-shadow: 0px 5px 5px rgba(203, 9, 231, 0.3), 0px -5px 5px rgba(50, 195, 198, 0.3);
    display: flex;
    align-items: center;
    justify-content: center;
    background: #040F1D;
}

.container {
    max-width: 1164px;
}

.roadmapBox, .faqContainer {
    max-width: 765px;
    margin: 0 auto;
}

.roadmapcont {
    min-height: 100px;
    padding: 20px 70px 10px 96px;
    display: flex;
    align-items: center;
    position: relative;
    z-index: 1;
    background: #040F1D;
    box-shadow: 0px 5px 5px rgba(203, 9, 231, 0.3), 0px -5px 5px rgba(50, 195, 198, 0.3);
}

.roadmapcont:after, .roadmapcont:before {
    position: absolute;
    z-index: -1;
    background: #040F1D;
    content: "";
}

.roadmapcont:before {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
}

.roadmapcont:after {
    top: 50%;
    height: 72%;
    width: 70px;
    display: block;
    transform: translateY(-50%) rotate(-45deg);
    left: 100%;
    box-shadow: 0px 5px 5px rgb(203 9 231 / 30%), 0px -5px 5px rgb(50 195 198 / 30%);
    z-index: -2;
    margin-left: -35px;
}

.faqContainer {
    margin-top: 80px;
}

.faqContainer h3 {
    margin-bottom: 30px;
}

.faqContainer .accordion-item {
    border: 1px solid #fff !important;
    border-radius: 0;
    margin-bottom: 30px;
    background: transparent;
}

.faqContainer .accordion-button {
    font-weight: bold;
    font-size: 18px;
    line-height: 22px;
    color: #fff;
    background: transparent;
    border-radius: 0;
    padding: 20px 15px;
    outline: none;
}

.faqContainer .accordion-button:not(.collapsed) {
    color: #fff;
    background: transparent;
}

.faqContainer .accordion * {
    border-radius: 0 !important;
}

.faqContainer .accordion-button::after {
    filter: invert(1) brightness(1000);
}

.faqContainer .accordion-button:focus {
    box-shadow: none;
}

.faqContainer .accordion-body {
    padding: 0 15px 40px;
}
